var nmb_prs_acharge,
    nmb_enfants_acharge,
    nmb_autres_prs_acharge,
    ressources_foyer,
    pensions_alimentaires,
    ressources_pec,
    div_garde_p,
    correctif,
    tauxprisencharge;

var plafond_legal = 1007;
var plafond_total, plafond25, plafond55 ;

function get_values () {
  ressources_foyer = Number(document.getElementById('input_ressourcesfoyer').value);
  pensions_alimentaires = Number(document.getElementById('input_palimentaires').value);
  nmb_autres_prs_acharge = Number(document.getElementById('input_autrespersacharge').value);
  nmb_enfants_acharge = Number(document.getElementById('input_enfantsacharge').value);
  if (document.getElementById('input_oui').checked) {
    div_garde_p = true;
  }
  else {
    div_garde_p = false;
  }
  console.log(div_garde_p);
}

function simuler () {
  if (div_garde_p) {
    nmb_enfants_acharge = nmb_enfants_acharge / 2;
    console.log("nombre d'enfants à charge pris en compte : " + nmb_enfants_acharge);
  }
  nmb_prs_acharge = nmb_autres_prs_acharge + nmb_enfants_acharge ;
  console.log("nombre de personnes à charge pris en compte : " + nmb_prs_acharge);
  if (nmb_prs_acharge > 2) {
    correctif = 360 + ((nmb_prs_acharge - 2) * 114);
  }
  else {
    correctif = nmb_prs_acharge * 180;
  }
  console.log("correctif pris en compte : " + correctif);
  plafond_total = plafond_legal + correctif
  plafond_total = Math.round(plafond_total);
  console.log("plafond total calculé : " + plafond_total);
  plafond55 = (plafond_legal * 1.182) + correctif
  plafond55 = Math.round(plafond55);
  console.log("plafond 55% calculé : " + plafond55);
  plafond25 = (plafond_legal * 1.499) + correctif
  plafond25 = Math.round(plafond25);
  console.log("plafond 25% calculé : " + plafond25);

  ressources_pec = ressources_foyer - pensions_alimentaires ;
  console.log("ressources prises en compte calculées : " + ressources_pec);
  tauxprisencharge = 0
  if (ressources_pec < plafond_total) {
    tauxprisencharge = 100;
  }
  if (ressources_pec > plafond_total && ressources_pec < plafond55) {
    tauxprisencharge = 55;
  }
  if (ressources_pec > plafond55 && ressources_pec < plafond25) {
    tauxprisencharge = 25;
  }
   console.log("Taux pris en charge : " + tauxprisencharge);

  document.getElementById('result').innerHTML = "Taux de prise en charge = " + tauxprisencharge + "%.";
  progressBar = document.getElementsByClassName('progress');
  progressBar[0].value = tauxprisencharge;
}

window.onload = function() {
  //annuler
  document.getElementById("input_annuler").addEventListener("click", function (){
    document.getElementById('input_ressourcesfoyer').value = "";
    document.getElementById('input_palimentaires').value = "";
    document.getElementById('input_autrespersacharge').value = "";
    document.getElementById('input_enfantsacharge').value = "";
    document.getElementById('input_oui').checked = false;
    document.getElementById('input_non').checked = false;
  });
  //simuler
  document.getElementById("simulate_btn").addEventListener("click", function () {
    get_values ();
    simuler ();
  });
}
